/* App.css */
/* 全域背景、字型預設 */
body, html, #root {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  font-family: "Noto Sans TC", "Microsoft JhengHei", Arial, sans-serif;
  background: #f7f7f7;
}

/* App 主內容容器響應式置中 */
.app-container {
  max-width: 960px;
  margin: 32px auto 24px auto;
  padding: 24px 24px 32px 24px;
  background: #fff;
  border-radius: 16px;
  box-shadow: 0 8px 32px rgba(50,50,55,0.07);
  display: flex;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
}

h1 {
  font-size: 2.2rem;
  text-align: center;
  color: #005353;
  margin-bottom: 10px;
  font-weight: bold;
  letter-spacing: 0.05em;
}

h2 {
  font-size: 1.3rem;
  text-align: center;
  color: #117a7a;
  margin-top: 26px;
  margin-bottom: 18px;
  font-weight: 600;
  letter-spacing: 0.03em;
}

@media (max-width: 700px) {
  .app-container {
    max-width: 99vw;
    padding: 15px 5px 20px 5px;
    border-radius: 0;
    box-shadow: none;
  }
  h1 {
    font-size: 1.15rem;
    margin-bottom: 7px;
  }
  h2 {
    font-size: 1rem;
    margin-top: 18px;
    margin-bottom: 10px;
  }
}
